.App {
  font-family: sans-serif;
  text-align: center;
}

.splitter {
  background-color: "#939393";
  position: relative;
  top: 50%;
}

.myContainer {
  margin: auto;
  display: flex;
  gap: 20px;
  width: 1024px;
  justify-content: center;
  margin-bottom: 40px;
}

.pane {
  flex: 1;
}

.coverImage {
  width: 1024px;
  height: auto;
  border: 1px solid gray;
}

@media screen and (max-width: 1024px) {
  .myContainer {
    flex-direction: column;
  }
  .pane {
    flex: auto;
  }
  .coverImage {
    width: 100%;
  }
}